var request = require('superagent')

const utils = {

  addManage: (manage) => {
    request
      .post('/manages')
      .send({ data: manage })
      .end(function (err, res) {
        if (err) {
          return console.error(err)
        }
        return res
      })
  },

  updateManage: (id, manage) => {
    request
      .put('/manages/' + id)
      .send({data: manage})
      .end(function (err, res) {
        if (err) {
          return console.error(err)
        }
        return res
      })
  },

  deleteManage: (id) => {
    request
      .del('/manages/' + id)
      .end(function (err, res) {
        if (err) {
          return console.error(err)
        }
        return res
      })
  }

}

export default utils



